<template>
  <div class="gallery-container">
    <div class="gallery-header">
      <h1> Galeria Zdj</h1>
      <button class="add-photo-btn" @click="showUploadModal = true">
         Dodaj zdjcie
      </button>
    </div>

    <!-- Upload Modal -->
    <div v-if="showUploadModal" class="modal-overlay" @click="closeUploadModal">
      <div class="modal-content" @click.stop>
        <div class="modal-header">
          <h2>Dodaj nowe zdjcie</h2>
          <button class="close-btn" @click="closeUploadModal"></button>
        </div>
        <div class="modal-body">
          <div class="upload-area" @drop="handleDrop" @dragover.prevent @dragleave.prevent>
            <input
              ref="fileInput"
              type="file"
              accept="image/*"
              @change="handleFileSelect"
              style="display: none"
            />
            <div v-if="!selectedFile" class="upload-placeholder" @click="$refs.fileInput.click()">
              <div class="upload-icon"></div>
              <p>Kliknij lub przecignij zdjcie</p>
            </div>
            <div v-else class="upload-preview">
              <img :src="previewUrl" alt="Preview" />
              <button class="remove-btn" @click="removeFile"></button>
            </div>
          </div>
          <div class="caption-input">
            <input
              v-model="newCaption"
              type="text"
              placeholder="Dodaj podpis (opcjonalne)"
              maxlength="255"
            />
          </div>
          <div class="modal-actions">
            <button class="cancel-btn" @click="closeUploadModal">Anuluj</button>
            <button class="upload-btn" @click="uploadPhoto" :disabled="!selectedFile || uploading">
              {{ uploading ? 'Przesyanie...' : 'Przelij' }}
            </button>
          </div>
        </div>
      </div>
    </div>

    <!-- Edit Modal -->
    <div v-if="editingPhoto" class="modal-overlay" @click="closeEditModal">
      <div class="modal-content edit-modal" @click.stop>
        <div class="modal-header">
          <h2>Edytuj zdjcie</h2>
          <button class="close-btn" @click="closeEditModal"></button>
        </div>
        <div class="modal-body">
          <div class="edit-tools">
            <button
              :class="['tool-btn', { active: editMode === 'crop' }]"
              @click="editMode = 'crop'"
            >
               Przytnij
            </button>
            <button
              :class="['tool-btn', { active: editMode === 'draw' }]"
              @click="editMode = 'draw'"
            >
               Rysuj
            </button>
            <button
              :class="['tool-btn', { active: editMode === 'text' }]"
              @click="editMode = 'text'"
            >
               Tekst
            </button>
            <button class="tool-btn" @click="resetEdit">
               Reset
            </button>
          </div>

          <div class="edit-canvas-container">
            <canvas ref="editCanvas" @mousedown="startDrawing" @mousemove="draw" @mouseup="stopDrawing" @mouseleave="stopDrawing"></canvas>
          </div>

          <div v-if="editMode === 'draw'" class="draw-options">
            <label>Kolor:</label>
            <input v-model="drawColor" type="color" />
            <label>Grubo:</label>
            <input v-model="drawWidth" type="range" min="1" max="20" />
          </div>

          <div v-if="editMode === 'text'" class="text-options">
            <input v-model="textToAdd" type="text" placeholder="Wpisz tekst" />
            <label>Kolor:</label>
            <input v-model="textColor" type="color" />
            <label>Rozmiar:</label>
            <input v-model="textSize" type="range" min="10" max="100" />
            <button @click="addTextToCanvas">Dodaj tekst</button>
          </div>

          <div class="modal-actions">
            <button class="cancel-btn" @click="closeEditModal">Anuluj</button>
            <button class="save-btn" @click="saveEditedPhoto">Zapisz</button>
          </div>
        </div>
      </div>
    </div>

    <!-- Photos Grid -->
    <div v-if="loading" class="loading">adowanie zdj...</div>
    <div v-else-if="photos.length === 0" class="empty-state">
      <div class="empty-icon"></div>
      <p>Brak zdj w galerii</p>
      <button class="add-first-btn" @click="showUploadModal = true">Dodaj pierwsze zdjcie</button>
    </div>
    <div v-else class="photos-grid">
      <div v-for="photo in photos" :key="photo.id" class="photo-card">
        <div class="photo-wrapper" @click="openPhotoView(photo)">
          <img :src="photo.url" :alt="photo.caption || 'Photo'" />
        </div>
        <div class="photo-info">
          <div v-if="editingCaptionId === photo.id" class="caption-edit">
            <input
              v-model="editedCaption"
              type="text"
              @keyup.enter="saveCaption(photo.id)"
              @blur="saveCaption(photo.id)"
              ref="captionInput"
            />
          </div>
          <div v-else class="caption-display" @click="startEditCaption(photo)">
            <p class="caption">{{ photo.caption || 'Kliknij aby doda podpis' }}</p>
          </div>
          <p class="upload-date">{{ formatDate(photo.uploadedAt) }}</p>
        </div>
        <div class="photo-actions">
          <button class="action-btn edit" @click="startEdit(photo)" title="Edytuj"></button>
          <button class="action-btn delete" @click="deletePhoto(photo.id)" title="Usu"></button>
        </div>
      </div>
    </div>

    <!-- Photo View Modal -->
    <div v-if="viewingPhoto" class="modal-overlay" @click="viewingPhoto = null">
      <div class="photo-view-modal" @click.stop>
        <button class="close-btn" @click="viewingPhoto = null"></button>
        <img :src="viewingPhoto.url" :alt="viewingPhoto.caption" />
        <div class="photo-view-info">
          <p v-if="viewingPhoto.caption" class="caption">{{ viewingPhoto.caption }}</p>
          <p class="date">{{ formatDate(viewingPhoto.uploadedAt) }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Gallery',
  props: {
    currentUser: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      photos: [],
      loading: false,
      showUploadModal: false,
      selectedFile: null,
      previewUrl: null,
      newCaption: '',
      uploading: false,
      editingPhoto: null,
      editMode: 'crop',
      drawColor: '#ff0000',
      drawWidth: 5,
      textColor: '#ffffff',
      textSize: 30,
      textToAdd: '',
      isDrawing: false,
      lastX: 0,
      lastY: 0,
      editingCaptionId: null,
      editedCaption: '',
      viewingPhoto: null,
      apiUrl: process.env.VUE_APP_API_URL || 'http://localhost:8000/api',
      originalImageData: null,
      editCanvas: null,
      editCtx: null
    }
  },
  mounted() {
    this.loadPhotos()
  },
  methods: {
    async loadPhotos() {
      this.loading = true
      try {
        const response = await fetch(`${this.apiUrl}/photos/${this.currentUser.id}`)
        const data = await response.json()
        this.photos = Array.isArray(data) ? data : []
      } catch (error) {
        console.error('Error loading photos:', error)
        alert('Bd podczas adowania zdj')
      } finally {
        this.loading = false
      }
    },
    handleFileSelect(event) {
      const file = event.target.files[0]
      if (file) {
        this.processFile(file)
      }
    },
    handleDrop(event) {
      event.preventDefault()
      const file = event.dataTransfer.files[0]
      if (file && file.type.startsWith('image/')) {
        this.processFile(file)
      }
    },
    processFile(file) {
      // Check file type - images and videos
      const validTypes = [
        'image/jpeg',
        'image/jpg',
        'image/png',
        'image/gif',
        'image/webp',
        'image/bmp',
        'video/mp4',
        'video/quicktime',
        'video/x-msvideo'
      ]
      if (!validTypes.includes(file.type.toLowerCase())) {
        alert(`Nieprawidowy typ pliku: ${file.type}. Dozwolone formaty: JPEG, JPG, PNG, GIF, WebP, BMP, MP4`)
        console.error('Invalid file type:', file.type)
        return
      }

      // Larger size limit for videos (50MB)
      const maxSize = file.type.startsWith('video/') ? 50 * 1024 * 1024 : 10 * 1024 * 1024
      if (file.size > maxSize) {
        const limit = file.type.startsWith('video/') ? '50MB' : '10MB'
        alert(`Plik jest za duy (max ${limit})`)
        return
      }

      console.log('File accepted:', file.name, file.type, file.size)
      this.selectedFile = file
      this.previewUrl = URL.createObjectURL(file)
    },
    removeFile() {
      this.selectedFile = null
      this.previewUrl = null
      if (this.$refs.fileInput) {
        this.$refs.fileInput.value = ''
      }
    },
    async uploadPhoto() {
      if (!this.selectedFile) return

      this.uploading = true
      console.log('Uploading file:', this.selectedFile.name, this.selectedFile.type)

      const formData = new FormData()
      formData.append('file', this.selectedFile)
      formData.append('userId', this.currentUser.id)
      formData.append('caption', this.newCaption)

      try {
        const response = await fetch(`${this.apiUrl}/photos`, {
          method: 'POST',
          body: formData
        })

        if (response.ok) {
          await this.loadPhotos()
          this.closeUploadModal()
          alert('Zdjcie dodane pomylnie!')
        } else {
          const error = await response.json()
          console.error('Upload failed:', error)
          let errorMsg = 'Bd: ' + (error.error || 'Nie udao si przesa zdjcia')
          if (error.receivedMimeType) {
            errorMsg += `\n\nTyp pliku: ${error.receivedMimeType}`
            errorMsg += `\nAkceptowane typy: JPEG, PNG, GIF, WebP, BMP`
          }
          alert(errorMsg)
        }
      } catch (error) {
        console.error('Upload error:', error)
        alert('Bd podczas przesyania zdjcia: ' + error.message)
      } finally {
        this.uploading = false
      }
    },
    closeUploadModal() {
      this.showUploadModal = false
      this.removeFile()
      this.newCaption = ''
    },
    startEdit(photo) {
      this.editingPhoto = photo
      this.editMode = 'crop'
      this.$nextTick(() => {
        this.initEditCanvas(photo.url)
      })
    },
    initEditCanvas(imageUrl) {
      const canvas = this.$refs.editCanvas
      if (!canvas) {
        console.error('Canvas not found')
        return
      }

      this.editCanvas = canvas
      this.editCtx = canvas.getContext('2d')

      const img = new Image()

      img.onerror = (error) => {
        console.error('Error loading image:', error)
        alert('Bd podczas adowania zdjcia. Sprbuj ponownie.')
      }

      img.onload = () => {
        const maxWidth = 800
        const maxHeight = 600
        let width = img.width
        let height = img.height

        if (width > maxWidth) {
          height = (height * maxWidth) / width
          width = maxWidth
        }
        if (height > maxHeight) {
          width = (width * maxHeight) / height
          height = maxHeight
        }

        canvas.width = width
        canvas.height = height
        this.editCtx.drawImage(img, 0, 0, width, height)
        this.originalImageData = this.editCtx.getImageData(0, 0, width, height)
        console.log('Canvas initialized successfully', width, height)
      }

      // Don't use crossOrigin for same-origin images
      // Only add if needed for external images
      if (imageUrl.startsWith('http') && !imageUrl.includes(window.location.hostname)) {
        img.crossOrigin = 'anonymous'
      }

      img.src = imageUrl
    },
    startDrawing(e) {
      if (this.editMode !== 'draw') return
      this.isDrawing = true
      const rect = this.editCanvas.getBoundingClientRect()
      this.lastX = e.clientX - rect.left
      this.lastY = e.clientY - rect.top
    },
    draw(e) {
      if (!this.isDrawing || this.editMode !== 'draw') return
      const rect = this.editCanvas.getBoundingClientRect()
      const x = e.clientX - rect.left
      const y = e.clientY - rect.top

      this.editCtx.beginPath()
      this.editCtx.moveTo(this.lastX, this.lastY)
      this.editCtx.lineTo(x, y)
      this.editCtx.strokeStyle = this.drawColor
      this.editCtx.lineWidth = this.drawWidth
      this.editCtx.lineCap = 'round'
      this.editCtx.stroke()

      this.lastX = x
      this.lastY = y
    },
    stopDrawing() {
      this.isDrawing = false
    },
    addTextToCanvas() {
      if (!this.textToAdd.trim()) return
      this.editCtx.font = `${this.textSize}px Arial`
      this.editCtx.fillStyle = this.textColor
      this.editCtx.fillText(this.textToAdd, 50, 50)
      this.textToAdd = ''
    },
    resetEdit() {
      if (this.originalImageData) {
        this.editCtx.putImageData(this.originalImageData, 0, 0)
      }
    },
    async saveEditedPhoto() {
      if (!this.editCanvas) return

      this.editCanvas.toBlob(async (blob) => {
        const formData = new FormData()
        formData.append('file', blob, 'edited.jpg')
        formData.append('userId', this.currentUser.id)
        formData.append('caption', this.editingPhoto.caption || '')

        try {
          // Upload as new photo
          const response = await fetch(`${this.apiUrl}/photos`, {
            method: 'POST',
            body: formData
          })

          if (response.ok) {
            await this.loadPhotos()
            this.closeEditModal()
            alert('Zdjcie zapisane!')
          }
        } catch (error) {
          console.error('Save error:', error)
          alert('Bd podczas zapisywania')
        }
      }, 'image/jpeg', 0.9)
    },
    closeEditModal() {
      this.editingPhoto = null
      this.editMode = 'crop'
    },
    startEditCaption(photo) {
      this.editingCaptionId = photo.id
      this.editedCaption = photo.caption || ''
      this.$nextTick(() => {
        if (this.$refs.captionInput && this.$refs.captionInput[0]) {
          this.$refs.captionInput[0].focus()
        }
      })
    },
    async saveCaption(photoId) {
      try {
        const response = await fetch(`${this.apiUrl}/photos/${photoId}`, {
          method: 'PATCH',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({ caption: this.editedCaption })
        })

        if (response.ok) {
          const photo = this.photos.find(p => p.id === photoId)
          if (photo) {
            photo.caption = this.editedCaption
          }
        }
      } catch (error) {
        console.error('Error saving caption:', error)
      } finally {
        this.editingCaptionId = null
        this.editedCaption = ''
      }
    },
    async deletePhoto(photoId) {
      if (!confirm('Czy na pewno chcesz usun to zdjcie?')) return

      try {
        const response = await fetch(`${this.apiUrl}/photos/${photoId}`, {
          method: 'DELETE'
        })

        if (response.ok) {
          this.photos = this.photos.filter(p => p.id !== photoId)
          alert('Zdjcie usunite')
        }
      } catch (error) {
        console.error('Error deleting photo:', error)
        alert('Bd podczas usuwania zdjcia')
      }
    },
    openPhotoView(photo) {
      this.viewingPhoto = photo
    },
    formatDate(dateString) {
      const date = new Date(dateString)
      return date.toLocaleDateString('pl-PL', {
        day: '2-digit',
        month: '2-digit',
        year: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      })
    }
  }
}
</script>

